.AddGigPage{
    .form-group{
        margin-bottom: 24px;
    }
    .add-group{
        margin-bottom: 50px;
    }
    .add-row{
        position: relative;
        display: flex;
        align-items: flex-start;
        justify-content: flex-start;
        flex-wrap: wrap;
        .left{
            text-align: right;
            width: 140px;
            padding-right: 20px;
        }
        .right{
            width: calc(100% - 140px);
        }
        .add-comment{
            position: absolute;
            right: 0;
            margin-top: 2px;
            color: #888;
        }
        
    }
    .codex-editor{
        padding:10px;
        border: 1px solid #ddd;
        margin:0;
    }
    .ce-block__content{
        max-width: 100%;
    }
    .ce-toolbar__content{
        max-width: 90%;
    }
    h1{
        font-size: 18px;
        font-weight: bold;
    }
    .form-control{
        color:#888
    }
    .DraftEditor-root, .DraftEditor-container{
        min-height: 200px;
        border:1px solid #ddd;
    }
    .public-DraftStyleDefault-block{
        margin: 0 5px;
    }
    .uploadedImage{
        width: 100px;
        position: relative;
        padding:5px;
        .remove{
            z-index: 2;
            color: #fff;
            background: #111;
            position: absolute;
            padding: 3px 4px;
            right: 10px;
            top: 10px;
            cursor: pointer;
            border-radius: 50%;
            line-height: 9px;
            font-size: 10px;
        }
    }
    .react-tags__search-input{
        width:100% !important;
    }
    .table input{
        border: 0!important;
        /* border-bottom: 1px solid #ddd!important; */
        padding: 0 0 0 35px;
        /* border-radius: 20px; */
        background: #eaeaea;
    }
    .input-icon{
        position: absolute;
        font-size: 12px !important;
        bottom: 35%;
        left: 14px;
    }
    td{
        position: relative;
    }
    .removeRow{
        position: absolute;
        right: 0;
        cursor: pointer;
        padding: 5px;
        background: #ddd;
        margin: 4px;
        top: 0;
    }
    .uploadArea{
        padding:30px;
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        border: 1px solid #ced4da;
        &.enter, &:hover{
            border: 2px dashed #444;
            background-color: #ddd;
            cursor: pointer;
        }
        .droppedFiles{
            
            display: flex;
            flex-wrap: wrap;
            .file{
                position: relative;
                margin: 10px;
                border:1px solid #ddd;
                .fa-times{
                    cursor: pointer;
                    position: absolute;
                    z-index: 2;
                    right: 0;
                    top:0;
                    padding:5px;
                    background-color: #ddd;
                }
            img{
                display: block;
                width: 150px;
            }
        }
        }
    }
}